import React from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Box,
  CircularProgress
} from '@mui/material';

interface DeleteConfirmationDialogProps {
  open: boolean;
  loading: boolean;
  documentName: string;
  onClose: () => void;
  onConfirm: () => void;
}

export const DeleteConfirmationDialog: React.FC<DeleteConfirmationDialogProps> = ({
  open,
  loading,
  documentName,
  onClose,
  onConfirm
}) => {
  return (
    <Dialog 
      open={open} 
      onClose={() => !loading && onClose()}
      disableRestoreFocus
    >
      <DialogTitle> </DialogTitle>
      <DialogContent>
             "{documentName}"?
      </DialogContent>
      <DialogActions>
        <Button 
          onClick={onClose}
          disabled={loading}
        >
          
        </Button>
        <Button 
          onClick={onConfirm} 
          color="error"
          disabled={loading}
        >
          {loading ? (
            <Box display="flex" alignItems="center">
              ... <CircularProgress size={20} sx={{ ml: 1 }} />
            </Box>
          ) : ''}
        </Button>
      </DialogActions>
    </Dialog>
  );
}; 